.articles {
  padding: 80px 0;
  &__title {
    text-align: center;
  }
  &__text {
    text-align: center;
    font-size: 24px;
    line-height: 150%;
    margin-top: 8px;
  }
  .links {
    margin-top: 40px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 8px;
  }
  .pagination {
    margin-top: 24px;
  }
}

@media screen and (max-width: 1200px) {
  .articles {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 992px) {
  .articles {
    padding: 40px 0;
    &__text {
      font-size: 14px;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .articles {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}