.main {
  padding: 60px 0 80px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: calc(100% - 650px);
  }
  &__right {
    width: 570px;
    padding-right: 45px;
    margin-left: 80px;
  }
  &__title {
    font-size: 100px;
    margin-top: 16px;
  }
  &__text {
    margin-top: 8px;
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 24px;
  }
  &__btn {
    width: 298px;
    height: 48px;
    margin: 0 24px 24px 0;
  }
  &__btn2 {
    width: 230px;
    height: 48px;
    margin-bottom: 24px;
  }
  &__slider {
    width: 100%;
    position: relative;
  }
  &__slider-nav {
    .swiper-button-prev {
      left: -45px;
    }
    .swiper-button-next {
      right: -45px;
    }
  }
  &-slide {
    border-radius: 40px;
    img {
      width: 100%;
      border-radius: 40px;
    }
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    overflow: unset;
    &-active,
    &-prev,
    &-next {
      opacity: 1;
      pointer-events: all;
    }
    &-prev {
      .main-slide__img {
        transform: rotate(-10deg) !important;
        margin-top: 0;
      }
    }
    &-next {
      .main-slide__img {
        transform: rotate(10deg) !important;
        margin-top: 0;
      }
    }
    .swiper-slide-shadow {
      background: transparent;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 56px;
  }
  &__item {
    display: flex;
    padding-left: 24px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 100%;
      background: #7163AA;
    }
    &-icon {
      flex-shrink: 0;
      width: 40px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-weight: 500;
      margin-left: 16px;
    }
  }
}
.info {
  position: relative;
  background: #F2F9FF;
  padding: 80px 0;
  margin: 120px 0;
  &__figure {
    position: absolute;
    top: -120px;
    left: 0;
    width: 100%;
    height: 120px;
  }
  &__figure2 {
    position: absolute;
    left: 0;
    bottom: -120px;
    width: 100%;
    height: 120px;
  }
  &__top {
    display: flex;
    align-items: center;
  }
  &__img {
    width: 345px;
    position: relative;
    margin-right: 45px;
    img {
      width: 100%;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 1;
    }
    img:nth-child(2) {
      position: absolute;
      left: 0;
      bottom: -30px;
    }
  }
  &__desc {
    width: calc(100% - 390px);
    max-width: 900px;
  }
  &__subtitle {
    margin-top: 8px;
  }
  &__text {
    margin-top: 16px;
    p:not(:first-child) {
      margin-top: 16px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
  }
  &__items {
    width: 800px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-right: 24px;
  }
  &__video {
    width: calc(100% - 824px);
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 24px;
    &-icon {
      width: 64px;
      height: 64px;
      margin-bottom: auto;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      margin-top: 16px;
    }
    &:hover {
      background: #ffffff;
      box-shadow: 0px 24px 24px -16px rgba(15, 15, 15, 0.20);
      border-radius: 24px;
      .info__item-icon {
        svg path {
          fill: #69A4D4;
        }
      }
      .info__item-text {
        font-weight: 700;
      }
    }
  }
  &__video-link {
    position: relative;
    display: block;
  }
  &__video-img {
    width: 100%;
    border-radius: 24px;
  }
  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 48px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 50%;
    img {
      width: 20px;
    }
  }
  &__video-figure {
    position: absolute;
    top: -55px;
    left: -55px;
    width: 189px;
    img {
      width: 100%;
    }
  }
}
