        right: -10px;
        width: 30px;
        height: 30px;
      }
    }
    .swiper-slide {
      border-radius: 20px;
      padding: 15px;
      img {
        border-radius: 20px;
      }
      &-prev {
        transform: rotate(-5deg);
      }
      &-next {
        transform: rotate(5deg);
      }
    }
  }
  .services {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .service-card {
    &__btn {
      width: 230px;
    }
  }
  .doctors {
    .container {
      padding: 0;
    }
    &__title {
      padding: 0 15px;
    }
    &__slider {
      padding: 0 0 0 15px;
    }
    .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
  .advantages {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .cooperation {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
      gap: 8px;
    }
  }
  .news {
    .swiper-slide {
      opacity: 1 !important;
      pointer-events: all !important;
    }
  }
}
@media screen and (max-width: 370px) {
  .main {
    &__items {
      grid-template-columns: 100%;
    }
  }
  .info {
    &__items {
      grid-template-columns: 100%;
    }
  }
}
