.footer {
  color: #ffffff;
  background: #7163AA;
  &-top {
    display: flex;
    justify-content: space-between;
    padding: 80px 0 40px;
  }
  &__items {
    display: flex;
    margin-right: 40px;
  }
  &__item {
    &:not(:first-child) {
      margin-left: 40px;
    }
    &-title {
      font-size: 32px;
      line-height: 120%;
      color: #ffffff;
    }
    &-list {
      margin-top: 24px;
      li {
        font-size: 18px;
        line-height: 120%;
        transition: all 0.3s;
      }
      li:hover {
        color: rgba(255, 255, 255, 0.5);
      }
      li:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__info {
    text-align: right;
  }
  &__address {
    &-title {
      font-size: 32px;
      line-height: 120%;
      font-weight: 700;
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      margin-top: 8px;
    }
  }
  &__phone {
    display: block;
    font-size: 40px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 40px;
    &:hover {
      color: rgba(255, 255, 255, 0.5);
    }
  }
  &__mail {
    display: block;
    font-size: 40px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 16px;
    &:hover {
      color: rgba(255, 255, 255, 0.5);
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ffffff;
    padding: 24px 0;
  }
  &__logo {
    width: 212px;
    display: block;
    margin-right: 40px;
    img {
      width: 100%;
    }
  }
  &__politics {
    font-size: 14px;
    line-height: 120%;
    a {
      text-decoration: underline;
      text-underline-offset: 3px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &-top {
      flex-direction: column;
      padding: 40px 0 80px;
    }
    &__items {
      justify-content: center;
      margin: 0 0 40px 0;
    }
    &__info {
      text-align: center;
    }
    &__item {
      &:not(:first-child) {
        margin-left: 24px;
      }
      &-title {
        font-size: 24px;
      }
      &-list {
        li {
          font-size: 14px;
        }
      }
    }
    &__address {
      &-title {
        font-size: 28px;
      }
      &-text {
        br {
          display: none;
        }
      }
    }
    &__phone {
      font-size: 28px;
    }
    &__mail {
      font-size: 28px;
    }
    &-bottom {
      flex-direction: column;
      text-align: center;
    }
    &__logo {
      margin: 0 0 24px 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__item {
      &:not(:first-child) {
        margin-left: 16px;
      }
    }
  }
}