.contacts {
padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
  }
  &__item {
    &-title {
      font-size: 16px;
      line-height: 120%;
      padding-left: 32px;
    }
    &-link {
      display: flex;
      align-items: center;
      margin-top: 5px;
      i {
        flex-shrink: 0;
        width: 24px;
      }
      i svg path {
        transition: all 0.3s;
      }
      i svg {
        width: 100%;
        height: auto;
      }
      span {
        font-size: 18px;
        line-height: 120%;
        color: #7163AA;
        margin-left: 8px;
        transition: all 0.3s;
      }
      &:hover {
        i svg path {
          stroke: #69A4D4;
        }
        span {
          color: #69A4D4;
        }
      }
    }
    &-map {
      font-size: 14px;
      line-height: 120%;
      text-decoration: underline;
      text-underline-offset: 3px;
      color: #7163AA;
      padding-left: 32px;
      margin-top: 8px;
      transition: all 0.3s;
      &:hover {
        color: #69A4D4;
      }
    }
  }
  .map {
    width: 100%;
    height: 600px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 40px 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__items {
      grid-template-columns: 100%;
      gap: 24px;
    }
    .map {
      height: 400px;
    }
  }
}