.doctors-main {
  position: relative;
  background: #ffffff;
  padding: 80px 0;
  margin: 0;
  .doctors__radial {
    bottom: -170px;
    opacity: 0.5;
    pointer-events: none;
  }
  .doctors__radial2 {
    top: 0;
    opacity: 0.5;
    pointer-events: none;
  }
  .doctors__slider {
    width: calc(100% + 30px);
    margin-top: 0;
    margin-left: -15px;
  }
  .swiper {
    padding: 30px 15px;
  }
  .swiper-wrapper {
    flex-direction: row;
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
    margin-top: 24px;
  }
  .swiper-pagination {
    bottom: -40px;
  }
}

@media screen and (max-width: 1200px) {
  .doctors-main {
    .doctors__slider {
      width: 100%;
      margin-top: 40px;
      margin-left: 0;
    }
    .swiper {
      overflow: unset;
      padding: 0;
    }
    .swiper-wrapper {
      flex-wrap: nowrap;
    }
    .swiper-slide {
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      margin-top: 0 !important;
      &-active,
      &-next,
      &-next + .swiper-slide {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .doctors-main {
    padding: 40px 0 80px;
  }
}

@media screen and (max-width: 768px) {
  .doctors-main {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .doctors-main {
    .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
}