.prices-main {
  position: relative;
  background: #F2F9FF;
  padding: 40px 0;
  &__radial {
    position: absolute;
    top: -70px;
    left: -70px;
    width: 460px;
    opacity: 0.5;
    pointer-events: none;
  }
  &__radial2 {
    position: absolute;
    right: -70px;
    bottom: -55px;
    width: 460px;
    opacity: 0.5;
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__desc {
    width: calc(100% - 500px);
    &-wrap {
      max-width: 685px;
    }
  }
  &__img {
    width: 450px;
    margin-left: 50px;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 80px;
    line-height: 100%;
  }
  &__text {
    font-size: 18px;
    line-height: 150%;
    margin-top: 8px;
  }
}
.prices {
  padding: 80px 0 80px;
}

@media screen and (max-width: 1200px) {
  .prices-main {
    &__title {
      font-size: 70px;
    }
  }
}

@media screen and (max-width: 992px) {
  .prices-main {
    &__radial {
      left: -140px;
      opacity: 0.1;
    }
    &__radial2 {
      display: none;
      opacity: 0.1;
    }
    &__desc {
      width: calc(100% - 374px);
    }
    &__img {
      width: 350px;
      margin-left: 24px;
    }
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .prices {
    padding: 40px 0;
  }
}

@media screen and (max-width: 768px) {
  .prices-main {
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
      text-align: center;
    }
    &__img {
      width: 100%;
      max-width: 350px;
      margin: 24px auto 0;
    }
  }
}