.article {
  padding: 80px 0;
  &__inner {
    display: flex;
  }
  &-sidebar {
    width: 330px;
    margin-right: 24px;
    &__wrap {
      background: #F2F9FF;
      border-radius: 24px;
      padding: 24px;
    }
    &__title {
      text-align: center;
      font-size: 24px;
      line-height: 120%;
      color: #7163AA;
    }
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 24px;
      li {
        font-size: 16px;
        line-height: 150%;
        list-style: auto;
        list-style-position: inside;
      }
    }
  }
  &__desc {
    width: calc(100% - 354px);
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    padding: 0 16px;
  }
  &__title {
    margin-bottom: 8px;
  }
  &__subtitle {
    text-align: center;
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
    color: #7163AA;
    margin-bottom: 24px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__info {
    position: relative;
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    padding-left: 16px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 5px;
      height: 100%;
      background: #7163AA;
      border-radius: 10px;
    }
    p {
      font-size: 24px;
      line-height: 150%;
    }
  }
}

@media screen and (max-width: 992px) {
  .article {
    padding: 40px 0;
    &__inner {
      display:block;
    }
    &-sidebar {
      width: 100%;
      margin: 0 0 40px 0;
      &__wrap {
        padding: 24px 16px;
      }
    }
    &__desc {
      width: 100%;
      gap: 24px;
      padding: 0;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__text {
      p {
        font-size: 14px !important;
      }
    }
    &__info {
      &:after {
        width: 3px;
      }
      p {
        font-size: 14px;
      }
    }
  }
}