.wrap {
  overflow: hidden;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rumpelstiltskin';
  line-height: 120%;
  font-weight: 400;
  color: #7163AA;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.btn-violet {
  background: #7163AA;
  border-radius: 24px;
  &:hover {
    background: darken(#7163AA, 10%);
  }
  span {
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
}
.btn-green {
  background: #A3C754;
  box-shadow: 0px 24px 24px -16px rgba(15, 15, 15, 0.20);
  border-radius: 24px;
  &:hover {
    background: darken(#A3C754, 10%);
  }
  span {
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  svg {
    flex-shrink: 0;
    width: 7px;
    height: auto;
    margin-left: 10px;
  }
}
.btn-violet-outline {
  background: transparent;
  border: 1px solid #7163AA;
  box-shadow: 0px 24px 24px -16px rgba(15, 15, 15, 0.20);
  border-radius: 24px;
  span {
    line-height: 120%;
    font-weight: 700;
    color: #7163AA;
  }
  svg {
    flex-shrink: 0;
    width: 7px;
    height: auto;
    margin-left: 10px;
  }
  &:hover {
    background: #7163AA;
    span {
      color: #ffffff;
    }
    svg path {
      stroke: #ffffff;
    }
  }
}
.text18 {
  font-size: 18px;
  line-height: 150%;
}
.text24 {
  font-size: 24px;
  line-height: 150%;
}
.text32 {
  font-size: 32px;
  line-height: 120%;
}
.text64 {
  font-size: 64px;
  line-height: 120%;
}
.swiper-button-prev {
  z-index: 1;
  width: 40px;
  height: 40px;
  background: #7163AA;
  border-radius: 8px;
  transition: all 0.3s;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 14px;
    height: auto;
    transform: rotate(-180deg);
  }
  &:hover {
    background: #FCFCFD;
    svg path {
      stroke: #7163AA;
    }
  }
}
.swiper-button-next {
  z-index: 1;
  width: 40px;
  height: 40px;
  background: #7163AA;
  border-radius: 8px;
  transition: all 0.3s;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 14px;
    height: auto;
  }
  &:hover {
    background: #FCFCFD;
    svg path {
      stroke: #7163AA;
    }
  }
}
.swiper-pagination {
  &-bullet {
    width: 40px;
    height: 4px;
    background: #69A4D4;
    border-radius: 0;
    opacity: 1;
  }
  &-bullet-active {
    background: #7163AA;
  }
}
.breadcrumbs {
  position: relative;
  z-index: 3;
  background: #ffffff;
  padding-top: 16px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    position: relative;
    color: #282828;
    margin-bottom: 16px;
    &:not(:last-child) {
      margin-right: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -15px;
        transform: translate(0, -50%);
        width: 4px;
        height: 8px;
        background-image: url("../img/arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        pointer-events: none;
      }
    }
    &:hover {
      color: #69A4D4;
      svg path {
        stroke: #69A4D4;
      }
    }
    &--active {
      &:hover {
        color: #282828;
      }
    }
    svg {
      width: 12px;
      height: auto;
    }
    svg path {
      transition: all 0.5s;
    }
  }
}
.tabs {
  &__content {
    display: none;
  }
  &__content--active {
    display: block;
  }
}
.nav {
  display: grid;
  grid-template-columns: 100%;
  gap: 24px;
  padding: 25px 0;
  a {
    font-size: 24px;
    line-height: 120%;
    color: #000000;
    &:hover {
      color: #7163aa;
    }
  }
}
.page-top {
  padding: 16px 0;
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 700;
  }
}
.accordions {
  display: grid;
  grid-template-columns: 100%;
  gap: 24px;
}
.accordion {
  background: #F2F9FF;
  border-radius: 24px;
  &-show {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px;
    cursor: pointer;
  }
  &-show__desc {
    margin-right: 24px;
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #7163AA;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    margin-top: 5px;
  }
  &__btn {
    font-size: 80px;
    line-height: 100%;
    font-weight: 300;
    color: #7163AA;
  }
  &-hidden {
    display: none;
    padding: 24px 40px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    li {
      display: flex;
      i {
        flex-shrink: 0;
        width: 14px;
      }
      i img {
        width: 100%;
        margin-top: 5px;
      }
      span {
        font-size: 18px;
        line-height: 150%;
        margin-left: 8px;
      }
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 3px;
        width: 100%;
        height: 1px;
        border-bottom: 1px dashed #7163AA;
      }
      span {
        position: relative;
        z-index: 1;
        font-size: 20px;
        line-height: 120%;
        font-weight: 700;
        color: #7163AA;
        background: #F2F9FF;
        padding: 3px 0;
      }
      span:nth-child(1) {
        margin-right: 24px;
      }
      span:nth-child(2) {
        text-align: right;
      }
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    p {
      font-size: 18px;
      line-height: 150%;
    }
  }
}
.links {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.link {
  height: 48px;
  padding: 3px 40px;
  margin-bottom: 16px;
  &:not(:last-child) {
    margin-right: 16px;
  }
  &--active {
    background: #7163AA;
    span {
      color: #ffffff;
    }
  }
  span {
    font-size: 18px;
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    font-size: 18px;
    line-height: 120%;
    color: #777E90;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #777E90;
    &:not(:first-child) {
      margin-left: 8px;
    }
    &:hover {
      color: #ffffff;
      background: #777E90;
    }
  }
}

@media screen and (max-width: 992px) {
  .text18 {
    font-size: 14px;
  }
  .text24 {
    font-size: 14px;
  }
  .text64 {
    font-size: 40px;
  }
  .page-top {
    &__title {
      font-size: 24px;
    }
  }
  .accordion {
    &-show {
      padding: 24px 8px;
    }
    &-hidden {
      padding: 24px 16px;
    }
    &__info {
      font-size: 14px;
    }
    &__btn {
      font-size: 60px;
    }
    &__list {
      grid-template-columns: 100%;
      li span {
        font-size: 14px;
      }
    }
    &__list2 {
      li span {
        font-size: 14px;
      }
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .links {
    flex-direction: column;
  }
  .link {
    width: 100%;
    margin: 0;
    &:not(:last-child) {
      margin: 0 0 16px 0;
    }
  }
}
