.doctor-main {
  padding: 80px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__img {
    width: 345px;
    position: relative;
    margin-right: 45px;
    img {
      width: 100%;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 1;
    }
    img:nth-child(2) {
      position: absolute;
      left: 0;
      bottom: -30px;
    }
  }
  &__desc {
    width: calc(100% - 390px);
    padding-top: 80px;
  }
  &__title {
    font-size: 32px;
    line-height: 120%;
    font-weight: 700;
    color: #7163AA;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    margin-top: 8px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin-top: 16px;
    li {
      font-size: 24px;
      line-height: 120%;
      list-style: disc;
      list-style-position: inside;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 24px;
  }
  &__btn {
    width: 288px;
    height: 48px;
    margin-right: 24px;
    margin-bottom: 10px;
    span {
      font-size: 18px;
    }
  }
  &__btn2 {
    width: 252px;
    height: 48px;
    margin-bottom: 10px;
    span {
      font-size: 18px;
    }
  }
  &__tabs {
    margin-top: 55px;
  }
  &__tabs-head {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__tab {
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    color: #B1B5C3;
    padding: 0 24px;
    position: relative;
    cursor: pointer;
    transition: all 0.5s;
    padding-bottom: 8px;
    margin-bottom: 24px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -8px;
      width: 100%;
      height: 1px;
      background: #B1B5C3;
    }
    &:hover {
      color: #7163AA;
      &:after {
        background: #7163AA;
      }
    }
  }
  .tab--active {
    color: #7163AA;
    &:after {
      background: #7163AA;
    }
  }
  &__tabs-body {
    padding-top: 16px;
  }
  &__text {
    p {
      font-size: 18px;
      line-height: 150%;
    }
    a {
      text-decoration: underline;
      text-underline-offset: 3px;
    }
    a:hover {
      color: #7163aa;
    }
  }
}
.services2 {
  position: relative;
  background: #F2F9FF;
  margin: 120px 0;
  &__figure {
    position: absolute;
    top: -120px;
    left: 0;
    width: 100%;
    height: 120px;
  }
  &__figure2 {
    position: absolute;
    left: 0;
    bottom: -120px;
    width: 100%;
    height: 120px;
  }
}

@media screen and (max-width: 992px) {
  .doctor-main {
    padding: 40px 0;
    &__inner {
      display: block;
    }
    &__img {
      width: 100%;
      max-width: 350px;
      margin: 0 auto 24px;
    }
    &__desc {
      width: 100%;
      padding-top: 0;
    }
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 16px;
    }
    &__list {
      li {
        font-size: 14px;
      }
    }
    &__text {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 576px) {
  .doctor-main {
    &__btns {
      flex-direction: column;
    }
    &__btn {
      width: 100%;
      margin: 0;
    }
    &__btn2 {
      width: 100%;
      margin: 16px 0 0 0;
    }
    &__tabs-head {
      flex-direction: column;
    }
    &__tab {
      width: 100%;
      text-align: center;
      padding: 0;
      margin: 0;
      &:not(:first-child) {
        margin-top: 24px;
      }
    }
    &__tabs-body {
      padding-top: 40px;
    }
  }
}