.service-main {
  background: #F2F9FF;
  position: relative;
  margin-bottom: 120px;
  .main__figure {
    position: absolute;
    left: 0;
    bottom: -120px;
    width: 100%;
    height: 120px;
  }
  .main__radial {
    position: absolute;
    top: -70px;
    left: -70px;
    width: 460px;
    opacity: 0.5;
    pointer-events: none;
  }
  .main__radial2 {
    position: absolute;
    right: -70px;
    bottom: 0;
    width: 460px;
    opacity: 0.5;
    pointer-events: none;
  }
}
.surgery {
  padding: 80px 0;
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    text-align: center;
    margin-bottom: 5px;
  }
  &__title {
    max-width: 900px;
    text-align: center;
    margin: 0 auto;
  }
  &__text {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    margin-top: 40px;
  }
  &__text-group {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
    ul li {
      font-size: 16px;
      line-height: 150%;
      list-style: disc;
      list-style-position: inside;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 24px;
  }
  &__img {
    img {
      width: 100%;
    }
  }
}
.works2 {
  background: #FCFCFD;
  margin: 0;
  overflow: hidden;
  .work-card {
    background: #ffffff;
    border: 1px solid #F2F9FF;
  }
}

@media screen and (max-width: 992px) {
  .service-main {
    .main__radial {
      top: -40px;
      left: -140px;
      opacity: 0.1;
    }
    .main__radial2 {
      display: none;
    }
  }
  .surgery {
    padding: 40px 0;
    &__text {
      grid-template-columns: 100%;
    }
    &__text-group {
      p {
        font-size: 14px;
      }
      ul li {
        font-size: 14px;
      }
    }
    &__images {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .surgery {
    &__images {
      grid-template-columns: 100%;
    }
  }
}