.faq {
  padding: 40px 0 80px;
  .accordions {
    margin-top: 8px;
  }
}
.feedback {
  padding: 80px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__desc {
    width: 675px;
    margin-right: 40px;
  }
  .form {
    width: calc(100% - 715px);
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
  }
  &__title {
    margin-top: 5px;
  }
  &__text {
    font-size: 18px;
    line-height: 150%;
    margin-top: 16px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    &-group {
      border: none;
      padding: 0;
      margin: 0;
      span {
        display: block;
        font-size: 16px;
        line-height: 120%;
        margin-bottom: 8px;
      }
      input {
        font-size: 16px;
        line-height: 120%;
        color: #1f222c;
        width: 100%;
        height: 48px;
        background-repeat: no-repeat;
        background-position: left 20px center;
        background-size: 20px;
        background-color: #FCFCFD;
        border: 1px solid #E6E8EC;
        border-radius: 24px;
        padding: 3px 16px 3px 48px;
        &::placeholder {
          color: #777E90;
        }
        &:focus {
          border-color: #1026E8;
          box-shadow: 0px 0px 0px 4px rgba(31, 81, 229, 0.08);
        }
      }
      textarea {
        font-size: 16px;
        line-height: 120%;
        color: #1f222c;
        width: 100%;
        height: 168px;
        background-repeat: no-repeat;
        background-position: left 20px top 12px;
        background-size: 20px;
        background-color: #FCFCFD;
        border: 1px solid #E6E8EC;
        border-radius: 24px;
        padding: 12px 16px 12px 48px;
        &::placeholder {
          color: #777E90;
        }
        &:focus {
          border-color: #1026E8;
          box-shadow: 0px 0px 0px 4px rgba(31, 81, 229, 0.08);
        }
      }
    }
    &__btn {
      width: 268px;
      height: 48px;
      span {
        font-size: 18px;
      }
    }
    &__info {
      position: relative;
      display: flex;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        cursor: pointer;
        opacity: 0;
      }
      input:checked ~ label {
        &:after {
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: 9px 7px;
        }
      }
      label {
        font-size: 16px;
        line-height: 150%;
        padding-left: 24px;
        position: relative;
        a {
          position: relative;
          z-index: 3;
        }
        a:hover {
          color: #7163aa;
        }
        &:after {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 16px;
          height: 16px;
          background: #7163AA;
          border-radius: 3px;
          transition: all 0.3s;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .feedback {
    &__desc {
      width: calc(100% - 529px);
      margin: 0;
    }
    .form {
      width: 505px;
      margin-left: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .faq {
    padding: 40px 0;
  }
  .feedback {
    padding: 40px 0;
    &__inner {
      display: block;
    }
    &__desc {
      width: 100%;
    }
    .form {
      width: 100%;
      margin: 40px 0 0 0;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__text {
      font-size: 14px;
    }
  }
}