.header {
  position: relative;
  z-index: 5;
  background: #ffffff;
  box-shadow: 0px 8px 16px -8px rgba(15, 15, 15, 0.20);
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 150px;
  }
  &__logo {
    width: 212px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    li:not(:first-child) {
      margin-left: 32px;
    }
    li:hover {
      span {
        color: darken(#7163AA, 15%);
      }
    }
    li a {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    li img {
      width: 32px;
      margin: 0 auto auto;
    }
    li span {
      font-size: 18px;
      line-height: 150%;
      color: #7163AA;
      margin-top: 8px;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  &__phone {
    &-number {
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
      color: #7163aa;
      &:hover {
        color: darken(#7163AA, 15%);
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #7163aa;
      margin-top: 5px;
    }
  }
  &__btn {
    width: 268px;
    height: 48px;
    padding: 3px 10px;
    margin-left: 24px;
    span {
      font-size: 18px;
    }
  }
  &__menu {
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    cursor: pointer;
    &-text {
      font-size: 18px;
      line-height: 120%;
      color: #7163AA;
      margin-top: 10px;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 24px;
  height: 16px;
  span {
    position: absolute;
    left: 0;
    height: 1px;
    background: #000000;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
    width: 100%;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
    width: 16px;
  }
  span:nth-child(3) {
    bottom: 0;
    width: 100%;
  }
  &--active {
    span:nth-child(1) {
      top: 8px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 7px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1250px) {
  .header {
    &__right {
      margin-left: 100px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__right {
      margin-left: 64px;
    }
    &__logo {
      width: 180px;
    }
    &__list {
      li:not(:first-child) {
        margin-left: 16px;
      }
      li img {
        width: 24px;
      }
      li span {
        font-size: 16px;
      }
    }
    &__info {
      margin-left: 16px;
    }
    &__phone {
      &-number {
        font-size: 18px;
      }
    }
    &__btn {
      width: 220px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      background: #ffffff;
      padding: 100px 0 50px 0;
      margin: 0;
      transition: all 0.5s;
      opacity: 0;
      pointer-events: none;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: all;
      }
    }
    &__logo {
      width: 212px;
    }
    &__list {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      li:not(:first-child) {
        margin: 0;
      }
    }
    &__info {
      flex-direction: column;
      margin: 32px 0 0 0;
    }
    &__phone {
      text-align: center;
    }
    &__btn {
      margin: 16px 0 0 0;
    }
    &__menu {
      display: flex;
    }
  }
}